














import { Component, Vue } from 'vue-property-decorator';
import { UserService } from './services/user.service';
import { Getter, Mutation } from 'vuex-class';
import NavBar from './components/NavBar.vue';
import { AuthStore } from './store/auth-store';
import { SocketService } from './services/socket.service';
import { MarkerService } from './services/marker.service';
import { MarksStore } from './store/marks-store';

@Component({
  components: {
    NavBar
  }
})
export default class App extends Vue {
  loggedIn = false;
  @Mutation initMarks!: () => void;

  async mounted() {
    this.loggedIn = !!AuthStore.state.jwt;
    this.$store.subscribe(() => {
      this.loggedIn = !!AuthStore.state.jwt;
    });
    this.loadInitialData();
  }

  // We dont need to wait for this. It can be loaded asynchronously
  loadInitialData() {
    this.loadMarks();
  }

  async loadMarks() {
    const markService = new MarkerService();
    MarksStore.state.marks = (await markService.getMarks()) || [];
    this.initMarks();
  }
}
